import React, { Component } from "react";
import { NavBar, Content, AboutMe, Contact } from "./components/Components";

class App extends Component {
  constructor(props) {
    super(props);
    this.aboutMeRef = React.createRef();
    this.contactMeRef = React.createRef();
  }

  scroll(ref) {
    window.scrollTo({
      top: ref ? ref.current.offsetTop : "",
      behavior: "auto"
    });
  }

  render() {
    return (
      <div className="container-fluid">
        <NavBar
          scrollRef={this.scroll}
          aboutMeProp={this.aboutMeRef}
          contactMeProp={this.contactMeRef}
        />
        <Content />
        <AboutMe aboutMeRefProp={this.aboutMeRef} />
        <Contact contactMeRefProp={this.contactMeRef} />
      </div>
    );
  }
}

export default App;
